.main-nav {
  margin-top: 19px;
  margin-bottom: 13px;
  display: flex;
}
.main-nav__link {
  margin: 0;
  padding: 0;
  z-index: 3;
  background-image: url("../img/logo/logo-white.png");
  background-repeat: no-repeat;
  width: 62px;
  height: 22px;
}
.main-nav__toggle {
  position: relative;
  top: -7px;
  left: 150px;
  display: block;
  width: 36px;
  height: 30px;
  z-index: 5;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  left: -10px;
  width: 36px;
  height: 2px;
  background-color: #C4C4C4;
  box-shadow: 0 8px 0 0 #C4C4C4, 0 16px 0 0 #C4C4C4;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #353559;
  box-shadow: 0 8px 0 0 #353559, 0 16px 0 0 #353559;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #c4c4c4;
  box-shadow: 0 8px 0 0 #c4c4c4, 0 16px 0 0 #c4c4c4;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: auto;
  left: 160px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -15px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #353559;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #c4c4c4;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    position: relative;
    top: -7px;
    left: 560px;
    display: block;
    width: 36px;
    height: 30px;
    z-index: 5;
    background-color: transparent;
    cursor: pointer;
    border: none;
    margin-right: 10px;
  }
  .main-nav {
    margin-bottom: 22px;
  }
  .main-nav--opened .main-nav__toggle {
    top: 0;
    right: auto;
    left: 480px;
    padding: 50px;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 80px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
  .main-nav__link {
    margin-top: 13px;
  }
}
